@import '../../seetings.scss';

@media screen and (min-width:320px) and (max-width:528px) {}

footer {
    width: 100%;
    height: auto;
    background-color: $color11;
    color: $color1;

    .footerContent {
        margin: 35px 35px 0px 35px;


            .content {
            
                    margin: 65px 35px 0px 35px;
                    display: flex;
                    justify-content: space-around;
                    align-content: center;
                    align-items: center;
                    flex-wrap: wrap;
            
                    color: $color1;
            
                    div {
                        margin-top: 35px;
            
            
            
                        h4 {
                            padding: 15px;
                            font-family: 'Montserrat';
                            font-weight: 500;
                            font-size: 14px;
                            line-height: 17px;
                        }
            
                        a {
            
                            font-family: 'Montserrat';
                            font-weight: 400;
                            font-size: 11px;
                            line-height: 25px;
                            color: $color1;
                            text-decoration: none;
                            transition: 1s all ease;
            
                            &:hover {
                                border-bottom: 1px solid $color1;
            
                            }
            
            
                        }
            
                        img {
                            margin-right: 5px;
                        }
                    }
            
                    .media {
            
                        a {
                            &:hover {
            
                                border: none;
                            }
            
                            img {
                                margin-right: 5px;
                            }
                        }
            
                        .medialogo {
                            display: flex;
                            flex-direction: row;
                            align-items: flex-start;
                            align-content: flex-start;
                            justify-content: flex-start;
                        }
                    }
                }

            // h2 {

            //     font-family: 'Montserrat' sans-serif;
             
            //     padding: 15px;
            //     text-transform: uppercase;
            //     text-align: center;
            //     padding: 15px;
            //     font-size: 12px;
                    


            // }

            // i {
            //     padding: 15px;
                
            // }
        }
    }

    .secondElementFooter {
        h2 {
            padding: 15px;
            text-transform: uppercase;
            text-align: center;
        }

        .secondElementitems {
          
            display: flex;
            justify-content: center;
            align-items: center;
            flex-wrap: wrap;

            .itemsfooter {
                width: 300px;
                height: 100px;

                p {
                    padding: 5px;
                }
            }
        }
    }

    h6 {
        font-family: 'Montserrat';
        font-weight: 400;
        font-style: normal;
        font-size: 14px;
        line-height: 18.0px;

        text-align: center;
        padding-bottom: 10px;
    }


@media screen and (min-width:529px) and (max-width:768px) {}

footer {
    width: 100%;
    height: auto;
    background-color: $color11;
    color: $color1;

    .footerContent {
        margin: 35px 35px 0px 35px;


        .firstElementFooter {
            h2{

                font-family: 'Montserrat';
                font-weight: 900;
                font-style: normal;
                font-size: 20px;
                line-height: 24px;
                padding: 15px;
                text-transform: uppercase;


            }

            i {
                padding: 15px;
            }
        }
    }

    .secondElementFooter {
        h2 {
            padding: 15px;
            text-transform: uppercase;
            text-align: center;
        }

        .secondElementitems {
            margin: auto;

            display: flex;
            justify-content: center;
            align-items: center;
            flex-wrap: wrap;

            .itemsfooter {
                width: 350px;
                height: 100px;

                p {
                    padding: 5px;
                }
            }
        }

        h6 {
            font-family: 'Montserrat';
            font-weight: 400;
            font-style: normal;
            font-size: 16px;
            line-height: 24.0px;

            text-align: center;
            padding-bottom: 15px;
        }
    }


}

@media screen and (min-width:769px) and (max-width:2500px) {
footer {
        width: 100%;
        height: auto;
        background-color: $color11;
        color: $color1;

        .footerContent {
            margin: 35px 35px 0px 35px;


            .firstElementFooter {
                display: flex;
                align-items: center;
                align-content: center;
                flex-direction: column;
                justify-content: center;

                h1 {

                    font-family: 'Montserrat';
                    font-weight: 900;
                    font-style: normal;
                    font-size: 20px;
                    line-height: 24px;
                    padding: 15px;
                    text-transform: uppercase;



                }

                i {
                    padding: 15px;
                }

                p {
                    width: 350px;
                }
            }
        }

        .secondElementFooter {
            h2 {
                padding: 15px;
                text-transform: uppercase;
                text-align: center;
            }

            .secondElementitems {
                margin: auto;

                display: flex;
                justify-content: center;
                align-items: center;
                flex-wrap: wrap;

                .itemsfooter {
                    width: 400px;
                    height: 100px;

                    p {
                        padding: 5px;
                    }
                }
            }

            h6 {
                font-family: 'Montserrat';
                font-weight: 400;
                font-style: normal;
                font-size: 16px;
                line-height: 24.0px;

                text-align: center;
                padding-bottom: 15px;
            }
        }


    }
}

