@import '../../seetings.scss';

//   Phone CSS
@media screen and (min-width: 320px) and (max-width: 768px) {
    nav {
        width: 100vw;
        height: 60px;
        background-color: $color3;
        border-bottom: 0.1rem solid $color2;
        position: fixed;
        z-index: 3;

        .container {
            position: relative;
            width: 100vw;
            display: flex;
            justify-content: space-around;

            .logo {
                margin: 5px;
                cursor: pointer;
                position: absolute;
                left: 25px;
                z-index: 4;
            }

            .links {
                .menu {
                    position: absolute;
                    top: 60px;
                    left: 0;
                    width: 100%;
                    background-color: $color3;
                    transition: all 1s ease;
                    transform: translateY(-120%);
                    z-index: 3;


                    li {
                        padding: 15px 25px;
                        z-index: 3;

                        a {
                            text-decoration: none;
                            text-transform: uppercase;
                            color: $color1;
                            font-family: 'Montserrat', sans-serif !important;
                            font-weight: 600;
                            font-size: 18px;
                            font-style: normal;
                            line-height: 27px;
                            z-index: 3;
                        }

                        i {
                            margin-left: 10px;
                            // transition: 3s ease all;
                        }

                    }

                    .langue {
                        z-index: 2;

                        a {
                            display: block;
                            text-decoration: none;
                            text-transform: uppercase;
                            color: $color2;
                            font-family: 'Montserrat', sans-serif !important;
                            font-weight: 600;
                            font-size: 18px;
                            font-style: normal;
                            line-height: 27px;
                            padding: 15px 50px;
                            background-color: $color1;
                            border-bottom: 1px solid $color5;

                            &:hover {
                                display: block;
                                background-color: $color5;
                            }
                        }

                    }

                }


            }

            .MenuBurger {
                width: 40px;
                position: absolute;
                top: 10px;
                right: 25px;
                font-size: 30px;
                color: $color1;
                cursor: pointer;
                transition: 2s ease all;
                opacity: 1s ease-out;
                z-index: 3;

                &:hover {
                    opacity: 1;
                    color: $color5;
                }

            }

            .menu.sticks {
                transform: translateY(0);
                width: 100vw;
                position: absolute;
                top: 60px;

                width: 100%;
                background-color: $color3;

            }
        }

    }

}

