@import '../../seetings.scss';

//   Phone 
@media screen and (min-width:320px) and(max-width:528px) {
    .servicesUtiles {
        margin-top: 100px;
        background-color: $color6;
        width: 100%;
        height: auto;
        display: flex;
        align-items: center;
        align-content: center;
        justify-content: center;
        flex-direction: column;
        

        h1 {
            font-family: 'Montserrat', sans-serif;
            font-weight: 900;
            font-style: normal;
            font-size: 40px;
            line-height: 48px;
            color: $color12 ;
            text-align: center;
            padding-top: 65px;

        }

        .elementutile {

            background-color: $color1;
            margin-top: 65px;
            margin-bottom: 45px;
            padding: 35px;
            height: 220px;
            margin-right: 45px;
            margin-left: 45px;
            border-radius: 10px;
            
            

            img {
                display: flex;
                justify-content: center;
                align-items: center;
                align-content: center;
                width:50% ;
                transform: translateX(50%);
                object-fit: cover;
              


            }

            h2 {
                margin-top: 20px;
                text-align: center;

                a {
                    font-family: 'Montserrat', sans-serif;
                    font-weight: 700;
                    font-style: normal;
                    font-size: 16px;
                    line-height: 24px;
                    color: $color12 ;
                    // text-decoration: none;

                }
            }


        }
    }
}

