@import '../../seetings.scss';

//    Phone
@media screen and (min-width:320px) and (max-width : 528px) {
    section {
        margin-top: 35px;
        background-color: $color10;
        height: 585px;
        display: flex;

        .left {
            display: none;
        }

        .right {
            width: 100%;

            h3 {
                text-align: center;
                padding-top: 25px;
                font-family: 'Montserrat', sans-serif;
                font-style: normal;
                font-weight: 400;
                font-size: 16px;
                line-height: 24px;
                color: $color9;
            }

            h2 {
                text-align: center;
                padding-top: 10px;
                font-family: 'Montserrat', sans-serif;
                font-style: normal;
                font-weight: 700;
                font-size: 28px;
                line-height: 34px;
                color: $color9;

            }

            img {
                width: 100%;
            }

            .buttonContainer {
                margin: auto;
                display: flex;
                justify-content: center;
                align-items: center;
                align-content: center;

                button {
                    width: 280px;
                    height: 46px;
                    background-color: $color3;
                    border: none;
                    opacity: .9;
                    cursor: pointer;

                    &:hover {
                        opacity: 1;
                    }

                    a {
                        text-decoration: none;
                        text-transform: uppercase;
                        color: $color1;
                        font-family: 'Montserrat', sans-serif !important;
                        font-weight: 600;
                        font-size: 18px;
                        font-style: normal;
                        line-height: 27px;
                        padding: 10px;


                    }


                }
            }
        }
    }
}

