@import '../../seetings.scss';

// Phone
@media screen and (min-width : 320px) and (max-width:528px) {
    main {
        margin: 35px 25px;
        position: relative;
        // background-color: red;
        padding-bottom: 85px;


        .elementtexte {
            display: flex;
            justify-content: space-between;
            align-items: center;
            align-content: center;
            flex-direction: column;

            h1 {
                color: $color9;
                font-family: 'Montserrat';
                font-weight: 900;
                font-style: normal;
                font-size: 40px;
                line-height: 48px;
            }

            button {
                position: absolute;
                bottom: 0;
                display: inline-block;
                background-color: $color1;
                width: 300px;
                height: 56px;
                border-color: $color8;
                border-width: 0.9px;

                &:hover {
                    background-color: $color8;
                    border: none;

                    a {
                        color: $color1;
                    }
                }

                a {
                    font: normal 700 16px/24px 'Montserrat';
                    text-decoration: none;
                    text-transform: uppercase;
                    color: $color8;

                    &:hover {
                        color: $color1;
                    }

                }

            }

        }

        .card {

            .cardContainer {
                box-shadow: 7px 7px 7px $color5;
                margin-top: 25px;
                padding-bottom: 25px;
                width: 100%;

                .cardImage {
                    background-color: orange;

                    img {
                        margin: auto;
                        display: block;
                        width: 100%;

                    }
                }

                .cardTitre {
                    h2 {
                        font: normal 700 20px/24px 'Montserrat', sans-serif;
                        color: $color9;
                        padding-top: 25px;
                        margin: 0 15px;
                    }
                }

                .cardTexte {
                    p {
                        font: normal 400 16px/24px 'Montserrat', sans-serif;
                        color: $color9;
                        padding-top: 25px;
                        margin: 0 15px;
                    }
                }

                .buttonContainer {
                    padding-top: 25px;
                    margin: 0 15px;

                    button {
                        width: 141px;
                        height: 47px;
                        background-color: $color4;
                        border: none;
                        opacity: .9;

                        cursor: pointer;

                        a {
                            text-decoration: none;
                            text-transform: uppercase;
                            color: $color2;
                            font-family: 'Montserrat', sans-serif !important;
                            font-weight: 600;
                            font-size: 18px;
                            font-style: normal;
                            line-height: 27px;
                            z-index: 3;
                        }
                    }
                }
            }
        }
    }
}

